/* Base Styles */
*,
*:before,
*:after {
  box-sizing: border-box;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%; }

body {
  font-family: "Helvetica", Helvetica Neue, Arial, sans-serif;
  overflow: hidden; }

[class|="col"] {
  margin-top: .5em;
  margin-bottom: .5em; }
  [class|="col"] span {
    text-align: center;
    padding: 1rem .5rem;
    width: 100%;
    display: block;
    font-weight: bold;
    font-size: 1.2rem; }

[class|="col-md"] span {
  background-color: violet;
  color: white; }

[class|="col-sm"] span {
  background-color: orangered;
  color: white; }

[class|="col-lg"] span {
  background-color: greenyellow;
  color: white; }

[class|="col-xl"] span {
  background-color: lightcoral;
  color: white; }

.container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: calc(1rem * 2);
  padding: 0 1rem;
  min-height: 0;
  min-width: 0;
  margin: auto;
  width: 100%; }
  .container--row-gap {
    grid-row-gap: calc(1rem * 2); }
  @media (min-width: 600px) {
    .container {
      width: 600px;
      max-width: 600px; } }
  @media (min-width: 900px) {
    .container {
      width: 900px;
      max-width: 900px; } }
  @media (min-width: 1200px) {
    .container {
      width: 1200px;
      max-width: 1200px; } }
  @media (min-width: 1800px) {
    .container {
      width: 1800px;
      max-width: 1800px; } }
  @supports not (display: grid) {
    .container {
      display: block; } }

.container-fluid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: calc(1rem * 2);
  padding: 0 1rem;
  min-height: 0;
  min-width: 0;
  margin: auto;
  width: 100%;
  max-width: 100%; }
  .container-fluid--row-gap {
    grid-row-gap: calc(1rem * 2); }
  @supports not (display: grid) {
    .container-fluid {
      display: block; } }

.row {
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  min-width: 0;
  max-width: calc(100% + calc(1rem * 2));
  margin: 0 -1rem;
  align-items: stretch; }
  @supports (display: grid) {
    .row {
      grid-column: 1 / -1; } }

